<template>
 <div id="popup" class="popup">
    <div class="popup__content">
      <div class="popup__body">
        <h3 class="popup__title">Налоговый вычет</h3>
        <p class="popup__text">Используйте налоговый вычет чтобы погасить ипотеку досрочно. Размер налогового вычета составляет не более 13%
          от своего официального годового дохода.</p>
        <Form @create='createSchedule' />
        <p v-if="payments.length" class="list">Итого можете внести в качестве досрочных:</p>
            <div v-for="(payment, idx) in payments" :key="idx" class="list__item">
              <input
                type="checkbox"
                class="list__checkbox"
                :id="idx"
              />
              <label :for="idx" class="list__label">
                {{ formatter(payment) }} рублей &nbsp;
                <span> в {{ getOrdinalNum(idx + 1) }} год </span>
              </label>
            </div>
        <div class="choice">
          <div class="choice__text">Что уменьшаем?</div>
          <div class="buttons">
            <label>
              <input type="radio" name="radio" checked />
              <div class="choice__payment box"><span>Платёж</span></div>
            </label>
            <label>
              <input type="radio" name="radio" />
              <div class="choice__period box"><span>Срок</span></div>
            </label>
          
          </div>
        </div>
     
      </div>
      <button class="btn btn_size_xxl">Добавить</button>
     
      <button class="close" @click="$emit('close')"></button>
    </div>

  </div>
</template>

<script>
import Form from './Form.vue'
export default {
  name: 'Card',
  data:()=> ({
    payments: ""
  }),
  components: {
    Form
  },
  methods: {
    createSchedule(data) {
      this.payments = data.payments
    },
    formatter(number) {
      return new Intl.NumberFormat('ru-RU').format(number)
    },
    getOrdinalNum(n) {
      return n+(1==n||4==n||5==n?'-ый':2==n||6==n||7==n||8==n?'-ой':'-ий')
    }
  }
}
</script>

