<template>
  <form class="form"
    @submit.prevent="createPayments()"
    
  >
    <div class="form__control" :class="{invalid: errors.name}" >
      <label>Ваша зарплата в месяц</label>
      <input class="form__input no-spinner" v-model.lazy="salary"
            placeholder="Введите данные"
            :type="isChange ? 'text' : 'number'"
            v-focus
            >
    
      <small>{{errors.name}}</small>
    </div>        

    <button class="form__btn" type="submit" >Рассчитать</button>
  </form>

</template>

<script>

export default {
  name: "Form",
  data() {
    return {
      salary: '',
      payments: [],
      errors: {
        name: null
      },
      isChange: false,
    };
  },
  methods: {
    formIsValid () {
      let isValid = true

      if (this.salary.length === 0 ) {
        this.errors.name = 'Поле обязательно для заполнения'
        isValid = false
      } else if (this.salary < 6000) {
        this.errors.name = 'Минимальное значение не менее 6000'
        isValid = false
      } else {
        this.errors.name = null
      }
      return isValid
    },
    createPayments(){
      if (this.formIsValid()) {
      const MAX_PRICE = 2000000
      let flatPrice = 2600000
      const MONTHS = 12
      const TAX_AMOUNT = 0.13
      const annualTax = this.salary * MONTHS * TAX_AMOUNT
      const maxTax = flatPrice < MAX_PRICE ? flatPrice * TAX_AMOUNT : 260000
      const yearsToPay = Math.ceil(maxTax / annualTax)

        
        for (let i = 1; i <= yearsToPay; i++) {
        if (i == yearsToPay) {
          let payment = maxTax % annualTax
          this.payments.push(payment)
        } else {
          this.payments.push(annualTax)
        }
      }
      
      this.create()
      console.log(typeof this.isChange)
      console.log(typeof this.salary)

      }
    },
    create(){
      this.$emit('create', {payments: this.payments });
      this.isChange = true
      this.payments = []
    }
  },
  directives: {
    focus: {
      mounted(el) {
        el.focus()
      }
    }
  } 
}
</script>

